<script>
    export let images;
    export let folder;
    export let showThumb = true;

	import { Swiper, SwiperSlide } from 'swiper/svelte';
	import 'swiper/swiper-bundle.css';
	import "swiper/components/navigation/navigation.min.css"
	import "swiper/components/thumbs/thumbs.min.css"
	import "../style.css";

    import SwiperCore, {  Navigation,Thumbs, Autoplay } from 'swiper/core';

    SwiperCore.use([Navigation,Thumbs, Autoplay]);

	let thumbsSwiper = null;

	const setThumbsSwiper = (e) => {
	const [swiper] = e.detail;
        // set Thumbs swiper instance
        setTimeout(() => {
            thumbsSwiper = swiper;
        });
	};
</script>

{#if showThumb}
<Swiper style="--swiper-navigation-color: #fff;--swiper-pagination-color: #fff" loop="{true}" spaceBetween="{10}" navigation="{true}" thumbs="{{ swiper: thumbsSwiper }}"
			autoplay='{{		"delay": 2500,		"disableOnInteraction": false	  }}' class="mySwiper2">
    {#each images as img}
    <SwiperSlide>
        <img alt="mainImage" src="https://cdn.jsdelivr.net/gh/Wedding1003/Wedding1003.github.io/images/{folder}/{img}.jpg"> 
    </SwiperSlide>
    {/each}
</Swiper>
<Swiper on:swiper="{setThumbsSwiper}" loop="{true}" spaceBetween="{10}" slidesPerView="{6}" freeMode="{true}" watchSlidesVisibility="{true}" class="mySwiper">
    {#each images as img}
    <SwiperSlide>
        <img alt="mainImage" src="https://cdn.jsdelivr.net/gh/Wedding1003/Wedding1003.github.io/images/{folder}/{img}.jpg"> 
    </SwiperSlide>
    {/each}
</Swiper>
{:else}
<Swiper style="--swiper-navigation-color: #fff;--swiper-pagination-color: #fff" loop="{true}" spaceBetween="{10}" navigation="{true}" class="mySwiper2">
    {#each images as img}
    <SwiperSlide>
        <img alt="mainImage" src="https://cdn.jsdelivr.net/gh/Wedding1003/Wedding1003.github.io/images/{folder}/{img}.jpg"> 
    </SwiperSlide>
    {/each}
</Swiper>
{/if}