<script>
    import CopyClipBoard from "./CopyClipBoard.svelte"
    import Space from "./Space.svelte"
	import Border from "./Border.svelte"

    let showKangmin = false;
	let showErin = false;

	function copy(val){
		const app = new CopyClipBoard({
			target: document.getElementById('clipboard'),
			props: { 'value' : val },
		});
		alert("복사되었습니다.")
		app.$destroy();
	}

	function showAccounts(selected){
		if(selected == "kang"){
			showErin = false;
			showKangmin = !showKangmin
		}else{
			showKangmin = false;
			showErin = !showErin
		}
	}
  </script>
  
  <Space value=3></Space>
  <Border></Border>
  <Space value=1></Space>
  <h3>마음 전하실 곳</h3>
  <Space value=2></Space>
  <div>
      <button class="accountBtn" on:click="{() => showAccounts("kang")}">신랑측 계좌번호 {showKangmin ? '닫기' : '보기'}</button>
      {#if showKangmin}
      <div class="accountWrap">
          <span class="info-small">농협은행 (예금주:이기주) <br> 013-12-076138</span>
          <button class="copyBtn" on:click={() => copy('01312076138')}>복사하기</button>
      </div>
      <div class="accountWrap">
          <span class="info-small">우리은행 (예금주:이강민) <br> 1002-647-945758</span>
          <button class="copyBtn" on:click={() => copy('1002647945758')}>복사하기</button>
      </div>
      {/if}
  </div>
  <Space value=1></Space>
  <div>
      <button class="accountBtn" on:click="{() => showAccounts("erin")}">신부측 계좌번호 {showErin ? '닫기' : '보기'}</button>
      {#if showErin}
      <div class="accountWrap">
          <span class="info-small">국민은행 (예금주:이은재) <br> 083-21-0186-867</span>
          <button class="copyBtn" on:click={() => copy('083210186867')}>복사하기</button>
      </div>
      <div class="accountWrap">
          <span class="info-small">국민은행 (예금주:이애린) <br> 812702-04-375475</span>
          <button class="copyBtn" on:click={() => copy('81270204375475')}>복사하기</button>
      </div>
      {/if}
  </div>

  <style>
      
	.info-small{
		font-family: 'GowunBatang-Bold';
		font-size: 1.0rem;
	}

    .accountBtn{
		padding: 0.4rem 2.4rem;
		border: none;
		background-color: #74644b;
		color: white;
		border-radius: 0.6rem;
		padding: 0.4rem 1.2rem;
	}

	.accountWrap {
		padding: 2rem 5rem 1rem 5rem;
		justify-content: space-around;
    	display: flex;
	}

	.copyBtn{
		margin-left: 1rem;
		font-weight: bolder;
		font-size: smaller;
		border: none;
		border-radius: 0.6rem;
		padding: 0.4rem 1.2rem;

	}

  </style>